#root {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

html {
	background: #fff;
}

.hover-label {
	opacity: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
}

.hover-label > div {
	width: 100%;
	height: 100%;
}

/* Button fills the wrapper fully */
.hover-label button {
	width: 100%;
	height: 100%;
	border-radius: 100%;
	background-color: rgba(255, 255, 255, 0.75);
	color: #000; /* black text to be readable */
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	border: none;
}
